import React,{useState, useRef} from 'react';
import { Card } from 'antd';

import SystemTabs from '../common/systemTabs';
import GroupList from './groupList';
import GroupDetail from './groupDetail';
import styles from './index.module.less';

function GroupManagement(){
    const grouplist = useRef();
    //初始化选择的分组
    const [groupId,setGroupId] = useState(null);
    //更新选择的分组
    const updateSelectedGroup = (record) => {
        // console.log(record);
        // record = {name:'groupName',id:'groupId001'}
        if(record){
            setGroupId(record.id);
        }else{
            setGroupId(null)
        }
    }
    //执行子组件的更新列表方法
    const updateGroupList = () => {
        grouplist.current.getGroupList();
    }

    return  <React.Fragment>
                <SystemTabs activeTab="groupManagement"/>
                <div style={{padding:20}}>
                    <Card title={false} bodyStyle={{padding:0}}>
                        <div className={styles.container}>
                            <GroupList updateSelectedGroup={updateSelectedGroup} selectedGroup={groupId} cRef={grouplist}/>
                            <GroupDetail selectedGroup={groupId} updateGroupList={updateGroupList}/>
                        </div>
                    </Card>
                </div>
            </React.Fragment>
}

export default GroupManagement;