import React,{useState,useEffect,useImperativeHandle} from 'react';
import {Button,Input,Modal, message, Icon, Form} from 'antd';
import {postAction,getAction} from '@/axios';
import styles from './index.module.less';

const MyIcon = Icon.createFromIconfontCN({
    scriptUrl:"//at.alicdn.com/t/font_1331187_no4dbj3r2x.js"
})

function GroupList({updateSelectedGroup,selectedGroup,cRef,...props}){
    const [searchValue,setSearchValue] = useState('');//搜索框内容
    const [groupList,setGroupList] = useState([]);//分组列表
    const [showModal,setShowModal] = useState(false);
    //获取分组列表
    useEffect(()=>{
        getGroupList();
    },[])
    //暴露给父组件的刷新方法
    useImperativeHandle(cRef, () => ({
        getGroupList:getGroupList
    }));
    //分组列表更新
    useEffect(() => {
        //如果有选择group则不做任何操作，如果没有，则默认选择第一条
        if(!selectedGroup){
            updateSelectedGroup(groupList[0] || {})
        }
    }, [groupList,selectedGroup,updateSelectedGroup])

    //获取分组列表
    let getGroupList = (value) => {
        let url =  `/ybt/system/setting/user/getUserGroupList?useType=0`;
        if(!(!value && Number(value)!==0)){
            url = `/ybt/system/setting/user/getUserGroupList?useType=0&groupNameKeyWord=${value}`
        }
        getAction(url).then(res=>{
            if(res.success){
                let groupList = res.obj?res.obj.map(group=>({name:group.groupName,id:group.groupId,haveUser:group.haveUser})):[];
                setGroupList(groupList);
            }
        }).catch(error=>{
            message.error(error)
        })
    }
    //添加分组---按钮
    const addGroup = () => {
        setShowModal(true);
    }
    //搜索---输入框
    const search = (value) => {
        getGroupList(value);
        // setSearchValue('');
    }
    //搜索--输入框--改变事件
    const change = (e) => {
        setSearchValue(e.target.value);
    }
    //删除---按钮
    const deleteGroup = (e,group) => {
        e.stopPropagation();
        console.log(group);
        //先判断是否有子成员(确定提示语句)
        let contextInfo = group.haveUser===1?'该分组下有子成员，是否确认删除？':'确认删除该分组吗？';  
        Modal.confirm({
            title:'删除分组确认',
            content:contextInfo,
            okText:'确认',
            cancelText:'取消',
            onOk:() =>{
                //发送删除请求
                getAction('/ybt/system/setting/user/delUserGroup?groupId='+group.id).then(deleteRes=>{
                    if(deleteRes.success){
                        //重新请求数据
                        //如果删除的分组，正是当前选择的分组，则设置选择的分组为空
                        if(group.id===selectedGroup){
                            getGroupList();
                            updateSelectedGroup()
                        }else{
                            getGroupList();
                        }
                        message.success('删除成功')
                    }else{
                        message.warn(deleteRes.obj)
                    }
                })
            }
        })
    }
    //点击行
    const clickRow = (group) => {
        updateSelectedGroup(group)
    }
    //确定新建分组按钮---弹窗
    const addGroupOk = () => {
        props.form.validateFields((err, values) => {
            if (!err) {
              postAction('/ybt/system/setting/user/saveUserGroup?groupName='+values.groupName).then(res => {
                  if(res.success){
                    setShowModal(false);
                    message.success('新增成功')
                    getGroupList();
                  }else{
                      message.warn(res.obj)
                  }
              })
            }
        });
    }
    const getFieldDecorator = props.form.getFieldDecorator;
    return (
        <div className={styles.groupList}>
            <div className={styles.btns}>
                <Button onClick={addGroup} type='primary'>新增分组</Button>
                <div style={{margin:"5px auto"}}></div>
                <Input.Search placeholder='输入分组名称进行模糊查询' value={searchValue} onSearch={search} onChange={change} allowClear={true}/>
            </div>
            <div className={styles.list}>
                {
                    groupList.map(item=>{
                        return <div key={item.id} onClick={()=>{clickRow(item)}} className={`${styles.row} ${item.id===selectedGroup?styles.active:''}`}>
                                    {item.name}
                                    <MyIcon type='icon-shanchu' onClick={(e)=>{deleteGroup(e,item)}}/>
                                </div>
                    })
                }
            </div>
            <Modal
                visible={showModal}
                title='新增分组'
                destroyOnClose={true}
                onOk={addGroupOk}
                onCancel={()=>{setShowModal(false)}}
            >
                <Form labelCol={{span:6}} wrapperCol={{span:18}}>
                    <Form.Item label="分组名称">
                        {getFieldDecorator('groupName', {
                            rules: [{ required: true, message: '请输分组名称！' }],
                        })(
                            <Input placeholder="请输分组名称" />,
                        )}
                    </Form.Item>
                </Form>
            </Modal>
        </div>
    )
}

export default Form.create()(GroupList);