import React, {useState,useEffect,useReducer } from 'react'
import { Button, Input, Checkbox, Table, Upload, Icon, Modal, message, Popconfirm } from 'antd';
import {postAction,getAction} from '@/axios';

import UserTree from './userTree';
import styles from './index.module.less';

const initGroupInfo = { groupName:'',users:[],forAll:false};
function reducer(state,action){
    switch(action.type){
        case 'getInfo'://获取信息
            return action.data;
        case 'changeName'://修改分组名称
            return {...state,groupName:action.groupName};
        case 'changeForAll'://修改权限
            return {...state,forAll:!state.forAll};
        case 'updateUser'://更新用户列表
            return {...state,users:action.users};
        case 'reset':
            return initGroupInfo;
        default :
            throw new Error();
    }
}
//将树转换成需要的结构
const transFormData = (data) => data.map(item => {
    if(item.userId){//人员
        return ({
            type:"user",
            value:item.userId,//唯一id
            name:item.name,//姓名
            userCode:item.userCode,//工号
            positionName:item.positionName,//职务
            departName:item.departName,//所属部门
            children:[]
        });
    }else{//部门
        let children = [...item.subList,...item.departUser];
        return({
            type:"depart",
            value:item.departId,
            name:item.departName,
            children:transFormData(children)
        }) 
    }
})

function GroupDetail({selectedGroup,...props}){
    // const [groupInfo,setGroupInfo] = useState({});
    const [groupInfo, dispatch] = useReducer(reducer,initGroupInfo);//分组详细信息
    const [condition,setCondition] = useState('');//筛选条件
    const [users,setUsers] = useState([]);//列表user信息
    const [showModal,setShowModal] = useState(false);//
    const [treeData,setTreeData] = useState([]);//全部用户树形菜单
    const [fileList,setFileList] = useState([])
    const columns = [
        {
            title:'序号',
            dataIndex:'sortIndex'
        },{
            title:'姓名',
            dataIndex:'userName'
        },{
            title:'工号',
            dataIndex:'userCode'
        },{
            title:'职务',
            dataIndex:'job'
        },{
            title:'所属部门',
            dataIndex:'depart'
        },{
            title:'操作',
            render:(text,record)=>{
                return <Popconfirm
                        title="确定删除该用户吗"
                        okText="确定"
                        cancelText="取消"
                        onConfirm={()=>{deleteUser(record)}}
                    >
                        <span className="operation-link">删除</span>
                    </Popconfirm>
            }
        }
    ]
    //初始化弹窗全部用户树
    useEffect(()=>{
        //发送请求获取数据
        postAction("/ybt/system/setting/getDepartUser",{departId:''}).then(res => {
            if(res.success){
                setTreeData(transFormData(res.obj))
            }else{
                message.error(res.obj);
            }
        })
        setTreeData([])
    },[])

    //初始化信息，修改选择用户组时触发
    useEffect(()=>{
        getGroupInfo(selectedGroup)
    },[selectedGroup])

    //当筛选条件，或者用户列表发生变化时，需要重新更新列表数据
    useEffect(()=>{
        let users = groupInfo.users.filter(user=>user.userName.indexOf(condition)!==-1);
        users.forEach((item,index)=>({...item,sortIndex:index+1}))
        setUsers(users)
    },[groupInfo,condition])

    //获取分组信息
    let getGroupInfo = (selectedGroup) => {
        if(selectedGroup){
            getAction('/ybt/system/setting/user/groupUsersGet?groupId='+selectedGroup).then(res=>{
                if(res.success){
                    let data = {
                        groupName:res.obj.groupName,
                        forAll:res.obj.useType===1,//是否全校可编辑
                        users:res.obj.sysUserGroupUsers.map((user,index)=>(
                            {
                                userName:user.name,//姓名
                                userCode:user.userName,//工号
                                userId:user.userId,//用户id
                                job:user.positionName,//职务
                                jobName:user.professionalTitle,//职称
                                depart:user.departName,//部门
                                sortIndex:index+1
                            }
                        ))
                    };
                    dispatch({type:'getInfo',data})
                }
            })
        }else{
            dispatch({type:'reset'})
        }
    }
    //更新分组信息
    // let updateGroup = () => {
        
    // }
    //修改过滤条件
    const changeCondition = (e) => {
        let value = e.target.value;
        setCondition(value);
    }
    //删除用户
    const deleteUser = (record) => {
        let users = groupInfo.users.filter(user=>user.userId!==record.userId).map((user,index)=>({...user,sortIndex:index+1}))
        dispatch({type:'updateUser',users})
    }
    //确认修改---按钮
    const handleUpdateGroup = () => {
        let params = {
            groupId:selectedGroup,
            groupName:groupInfo.groupName,
            useType:groupInfo.forAll?1:0,
            sysUserGroupUsers:groupInfo.users.map(user=>(
                {
                    name: user.userName,
                    userId: user.userId,
                    userName: user.userCode
                }
            ))
        }
        postAction('/ybt/system/setting/user/saveGroupUsers',params).then(res=>{
            if(res.success){
                message.success('修改成功');
                //重新请求左侧列表
                props.updateGroupList();
            }else{
                message.error(res.msg)
            }
        })
    }
    //新增人员弹窗---按钮
    const handleOpenModal = () => {
        setShowModal(true);
    }
    //上传操作---按钮
    const uploadProps = {
        fileList:fileList,
        beforeUpload:(file)=>{
            setFileList([file])
        },
        showUploadList:false,
        name: 'file',
        action: '/ybt/system/setting/user/importGroupUsers?groupId='+selectedGroup,
        headers: {
            authorization: 'authorization-text',
        },
        onChange(info) {
            if (info.file.status !== 'uploading') {
                // console.log(info.file, info.fileList);
            }
            if (info.file.status === 'done') {//上传完成
                // message.success(`${info.file.name} file uploaded successfully`);
                try {
                    let list = info.fileList[0].response.obj;
                    let users = list.map((item,index)=>({
                        userName:item.name,
                        userCode:item.userName,
                        job:item.positionName,
                        depart:item.departName,
                        sortIndex:index+1,
                        userId:item.userId,//唯一id
                    }));
                    dispatch({type:'updateUser',users});
                    message.success('上传成功')
                } catch (error) {
                    message.success('上传错误')
                }
            } else if (info.file.status === 'error') {
                message.error(`${info.file.name} 上传失败`);
            }
        }
    }
    //下载人员倒入模版---按钮
    const handleDownLoad = () => {
        window.open(`http://${window.location.host}/groupTemplate.xlsx`,'_blank')

    }
    //更新弹窗选择的人员
    const handleUpdateUser = (data) => {
        let users = data.map((user,index)=>({
            userName:user.name,//姓名
            userCode:user.userCode,//工号
            userId:user.userId,//用户id
            job:user.positionName,//职务
            depart:user.departName,//部门
            sortIndex:index+1
        }));
        dispatch({type:'updateUser',users:users})
        setShowModal(false);
    }
    return (
        <div className={styles.groupDetail}>
            <div className={styles.title}>
                分组名称：
                <Input style={{width:'320px',margin:'0 20px'}} placeholder='分组名称' value={groupInfo.groupName} onChange={(e)=>{dispatch({type:'changeName',groupName:e.target.value})}}/>
                <Checkbox onChange={()=>{dispatch({type:'changeForAll'})}} checked={groupInfo.forAll}>允许全校编辑使用</Checkbox>
            </div>
            <div className={styles.options}>
                人员查询：
                <Input allowClear={true} style={{width:'320px',marginLeft:'20px'}} placeholder='输入名称模糊查询' onChange={changeCondition}/>
                <div className={styles.btns}>
                    <Button onClick={handleOpenModal} style={{marginRight:'10px'}}>新增人员</Button>
                    <Upload {...uploadProps}>
                        <Button>
                            <Icon type="upload" /> 上传人员列表
                        </Button>
                    </Upload>
                    <Button onClick={handleDownLoad} style={{marginLeft:'10px'}}>下载人员导入模版</Button>
                </div>
            </div>
            <div className={styles.userList}>
                <Table
                    columns={columns}
                    dataSource={users}
                    rowKey='userCode'
                    pagination={false}
                />
            </div>
            <div className={styles.footer}>
                <Button type='primary' onClick={handleUpdateGroup}>确认修改</Button>
            </div>
            <Modal
                visible={showModal}
                title='选择人员'
                footer={null}
                onCancel={()=>{setShowModal(false)}}
                width='960px'
                bodyStyle={{height:'70vh',overflow:'hidden',display:'flex',flexDirection:'column'}}
                // destroyOnClose={true}
            >
                <UserTree
                    onOk={handleUpdateUser}
                    onCancel={()=>{setShowModal(false)}}
                    treeData={treeData}
                    selectedUsers={groupInfo.users}
                />
            </Modal>
        </div>
    )
}

export default GroupDetail;
