import React,{useEffect,useState} from 'react';
import {Tree,Button, Select, message} from 'antd';
import {debounds} from '@/util';
import {getAction,postAction} from '@/axios';
import styles from './index.module.less';

const { TreeNode } = Tree;

//将树形结构转为key，value 一级数据
const fattenTreeList = (data) => {
    return data.reduce((list,item)=>{
        if(item.type==='depart'){
            return list.concat(fattenTreeList(item.children))
        }else{
            return list.concat({key:item.value,name:item.name})
        }
    },[])
}


function UserTree({treeData,selectedUsers,...props}){
    // console.log(treeData)
    const [type,setType] = useState('user');//搜索类型user，depart
    const [expandedKeys,setExpendedKeys] = useState([]);//展开的key
    const [checkedUsers,setCheckedUsers] = useState(selectedUsers);//选中的用户（包括详情）
    const [checkedUserIds,setCheckedUserIds] = useState([]);//选中的用户Id
    const [searchValue,setSearchValue] = useState('');//搜索框内容
    const [searchOption,setSearchOption] = useState([]);//搜索的结果
    const [autoExpandParent,setAutoExpandParent] = useState(true);
    const [fattendTreeData,setFattendTreeData] = useState([]);
    useEffect(()=>{
        let data = fattenTreeList(treeData);
        setFattendTreeData(data)
    },[treeData])
    useEffect(()=>{
        let users = selectedUsers.map(item=>({
            userId:item.userId,
            name:item.userName,
            type:'user',
            userCode:item.userCode
        }))
        setCheckedUsers(users)
    },[selectedUsers])
    //
    useEffect(()=>{
        setCheckedUserIds(checkedUsers.map(user=>user.userId))
    },[checkedUsers])
    //渲染树形结构
    const renderTreeNodes = (data)=>{
        return data.map((item) => {
            let title = item.type==="user"?<span>{item.name} ({item.userCode})</span>:<span>{item.name}</span>;

            if (item.children) {
                return (
                    <TreeNode title={title} key={item.value} isLeaf={item.type==="user"?true:false} dataRef={item}>
                        {renderTreeNodes(item.children)}
                    </TreeNode>
                );
            }
            return <TreeNode title={title} key={item.value}  isLeaf={item.type==="user"?true:false} dataRef={item}/>;
        });
    }
    //展开操作
    const handleExpand=(expandedKeys)=>{
        setExpendedKeys(expandedKeys)
        setAutoExpandParent(false);
    }
    //勾选功能
    const handleCheck=(checkedKeys,e)=>{
        const checkedNodes = e.checkedNodes.map(
            item => ({
                userId:item.props.dataRef.value,//用户id
                name:item.props.dataRef.name,//姓名
                type:item.props.dataRef.type,//类型
                userCode:item.props.dataRef.userCode,//工号
                positionName:item.props.dataRef.positionName,
                departName:item.props.dataRef.departName
            })
        )
        setCheckedUsers(checkedNodes);
    }
    //搜索框改变事件
    const handleChange = (value,detail) => {
        setSearchValue(value);
        const newCheckedUserIds = Array.from(new Set([...checkedUserIds,value]));
        setCheckedUserIds(newCheckedUserIds);
        console.log(detail)
        // let checkedNodes = [...checkedUsers,{userId:}]
        // const checkedNodes = e.checkedNodes.map(
        //     item => ({
        //         userId:item.props.dataRef.value,//用户id
        //         name:item.props.dataRef.name,//姓名
        //         type:item.props.dataRef.type,//类型
        //         userCode:item.props.dataRef.userCode,//工号
        //         positionName:item.props.dataRef.positionName,
        //         departName:item.props.dataRef.departName
        //     })
        // )
        // setCheckedUsers(checkedNodes);
        if(type==='user'){
            let checkedNodes = [...checkedUsers,{
                userId:value,
                name:detail.props.detail.text,
                type:'user',
                userCode:detail.props.detail.userCode,
                positionName:detail.props.detail.positionName,
                departName:detail.props.detail.departName
            }];
            setCheckedUsers(checkedNodes)
        }else{
            const newExpendKeys = Array.from(new Set([...expandedKeys,value]));
            setExpendedKeys(newExpendKeys);
            let userIds = [];
            let fattenData = (data,users) => data.reduce((users,item) => {
                if(item.userId){//人员
                    return users.concat({
                        type:"user",
                        value:item.userId,
                        name:item.name,
                        userCode:item.userCode
                })
                }else{//部门
                    let children = [...item.subList,...item.departUser];
                    return fattenData(children,users)
                }
            },users);
            postAction("/ybt/system/setting/getDepartUser",{departId:value}).then(res => {
                if(res.success){
                    let users = [];
                    users = fattenData(res.obj,[]);
                    let checkedNodes = [...checkedUsers,...users.map(item=>({type:'user',userId:item.value,name:item.name,userCode:item.userCode,positionName:item.positionName,departName:item.departName}))];
                    checkedNodes = checkedNodes.filter(item=>{
                        if(userIds.indexOf(item.userId)===-1){
                            userIds.push(item.userId);
                            return true;
                        }else{
                            return false;
                        }
                    })
                    setCheckedUsers(checkedNodes)
                }else{
                    message.error(res.obj);
                }
            })
        }
    }
    //右侧删除选中人员
    const handleDelete = (userId) => {
        let checkedNodes = checkedUsers.filter(item=>item.userId!==userId);
        let selectedKeys = checkedNodes.filter(item => item.type==="user").map(item=>item.userId);
        setCheckedUsers(checkedNodes.filter(item => item.type==="user"))
        setCheckedUserIds(selectedKeys);
    }
    //搜索框搜索事件---（选择后还会触发一次请求,待优化）
    const handleSearch = debounds((value)=>{
        if(value){
            if(type==='user'){//查询用户
                postAction('/ybt/system/setting/user/getUserListByName',{keyword:value}).then(res=>{
                    // console.log(res);
                    if(res.success){
                        setSearchOption(res.obj.departUser.map(item=>({text:item.name,value:item.userId,userCode:item.userCode,positionName:item.positionName,departName:item.departName,type:'user'})))
                    }else{
                        message.error(res.msg)
                    }
                })
            }else{//查询部门
                getAction('/ybt/system/setting/depart/getDepartListForQuery?departName='+value).then(res=>{
                    if(res.success){
                        setSearchOption(res.obj.map(item=>({text:item.departName,value:item.departId})))
                    }else{
                        message.error(res.msg)
                    }
                })
            }
        }else{
            setSearchOption([])
        }
    },300)
    //确认---按钮
    const handleOk = () => {
        props.onOk(checkedUsers.filter(item=>item.type==='user'))
    }
    //取消---按钮
    const handleCancle = () => {
        props.onCancel();
    }
    return (
        <React.Fragment>
            <div className={styles.searchBox}>
                <Select value={type} onChange={(value)=>{setType(value)}}>
                    <Select.Option value='user'>人 员</Select.Option>
                    <Select.Option value='depart'>部 门</Select.Option>
                </Select>
                <Select
                    showSearch
                    placeholder={'请输入需要查询的内容'}
                    value={searchValue} 
                    onChange={handleChange} 
                    onSearch={handleSearch} 
                    defaultActiveFirstOption={false}
                    showArrow={false}
                    filterOption={false}
                    notFoundContent={null}
                    style={{width:'260px'}}
                >
                    {
                        searchOption.map(item=>{
                            return <Select.Option key={item.value} detail={item}>{item.text}</Select.Option>
                        })
                    }
                </Select>
            </div>
            <div className={styles.userTreeContainer}>
                <Tree
                    className={styles.userTree}
                    checkable={true}
                    onCheck={handleCheck}
                    onExpand={handleExpand}
                    expandedKeys={expandedKeys}
                    checkedKeys={checkedUserIds}
                    autoExpandParent={autoExpandParent}
                >
                    {
                        renderTreeNodes(treeData)
                    }
                </Tree>
                <div className={styles.selectedList}>
                    {
                        checkedUsers.filter(userItem=>fattendTreeData.find(item=>item.key===userItem.userId)).map(user=>{
                        return (
                            <div style={{padding:'8px 20px'}} key={user.userId}>
                                {user.name}
                                <span style={{float:'right',cursor:'pointer'}} onClick={()=>{handleDelete(user.userId)}}>X</span>
                            </div>
                        )
                        })
                    }
                </div>
            </div>
            <div className={styles.userTreeFooter}>
                <Button onClick={handleOk} style={{marginRight:'20px'}}>确 认</Button>
                <Button onClick={handleCancle}>取 消</Button>
            </div>
        </React.Fragment>
    )
}

export default UserTree;